@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  font-family: "Roboto", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
.App {
  max-width: 100%;
  width: 100%;
}

*{margin:0;padding:0;box-sizing:border-box}@media screen and (max-width: 400px){body{font-size:10px}}@media screen and (min-width: 800px){body{font-size:14px}}html{font-size:calc(10px + (14 - 10) * ((100vw - 400px) / (800 - 400)))}body{font-family:sans-serif;background:#260f26}.page{width:90%;max-width:1000px;margin:3rem auto;box-shadow:0 0 1rem rgba(0,0,0,0.5);background:#361f36}.article{padding:5%}.article__title{margin-bottom:1em;color:#fff;opacity:0.8}.article__paragraph{margin-bottom:0.6em;line-height:1.5;color:#fff;opacity:0.6}.menu{display:flex;width:90%;max-width:1000px;margin:3rem auto;box-shadow:0 0 1rem rgba(0,0,0,0.5);background:#361f36}.menu__item{position:relative;flex-grow:1;text-align:center;line-height:3rem;cursor:pointer;text-decoration:none;text-transform:uppercase;color:#fff;opacity:0.6;border-bottom-width:0.2rem;border-bottom-style:solid;border-bottom-color:transparent}.menu__item:hover{opacity:1;border-bottom-color:#bfbf40}.menu__item-active,.menu__item-active:hover{opacity:1;border-bottom-color:#40bf40}.menu__item.active,.menu__item.active:hover{opacity:1;border-bottom-color:#40bf40}

